body{
  overflow-x: hidden;
}

.main-image {
  background: url(https://images.unsplash.com/photo-1535905748047-14b2415c77d5?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100wh;
  height: 80vh;
  position: relative;
  background-attachment: fixed;
}

.filter:after {
  background-color: rgba(0, 0, 0, 0.4);
  content: '';
  display: block;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.card-title {
  overflow: hidden;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
  font-size: 16px;
  white-space: nowrap;
  text-overflow: ellipsis;
}

label, Input {
font-family: 'Courier New', Courier, monospace;
}

#maxResults, #startIndex {
  font-family: fantasy;
  font-size: 16px;
}

.card-image {
  margin-left: '20px'
}